<template>
  <div class="container" style="margin: 15px 15px;">
    <!-- 页面名字 -->
    <el-row type="flex" justify="start" style=" font-size: 16px; color: #303133;">
      <router-link class='el-button--text' to="/property/list">楼盘管理</router-link>
      / {{pageTitle}}
    </el-row>
    <el-row type="flex" justify="start" style="margin-top: 30px;">
      <el-form ref="form" :model="form" label-width="100px" label-suffix=":" label-position="left" style="width:100%">
        <h1 style="font-size: 20px">基本信息</h1>
        <el-form-item label="楼盘名称" prop="name" required>
          <el-input v-model="form.name" placeholder="请输入楼盘名称" />
        </el-form-item>
        <el-form-item label="封面图" prop="cover" required>
          <div v-if="form.cover && form.cover.id" class="avatar">
            <img :src="'https://szebra.cn' + form.cover.url" />
            <i class="el-icon-circle-close" @click="removeConverImageUrl" />
          </div>
          <el-upload v-else class="avatar-uploader"
                    action="https://szebra.cn/api/admin/images"
                    :show-file-list="false"
                    :on-success="handleAvatarSuccess"
                    name="image[file]"
                    :before-upload="beforeAvatarUpload">
            <i class="el-icon-plus avatar-uploader-icon" />
          </el-upload>
        </el-form-item>
        <el-form-item label="海报图片" prop="poster" required>
          <div v-if="form.poster && form.poster.id" class="avatar">
            <img :src="'https://szebra.cn' + form.poster.url" />
            <i class="el-icon-circle-close" @click="removePosterImageUrl" />
          </div>
          <el-upload v-else class="avatar-uploader"
                    action="https://szebra.cn/api/admin/images"
                    :show-file-list="false"
                    :on-success="handlePosterAvatarSuccess"
                    name="image[file]"
                    :before-upload="beforeAvatarUpload">
            <i class="el-icon-plus avatar-uploader-icon" />
          </el-upload>
        </el-form-item>
        <!-- 地图图片 -->
        <el-form-item label="地图图片" prop="map" required>
          <div v-if="form.map && form.map.id" class="avatar">
            <img :src="'https://szebra.cn' + form.map.url" />
            <i class="el-icon-circle-close" @click="removeMapImageUrl" />
          </div>
          <el-upload v-else class="avatar-uploader"
                    action="https://szebra.cn/api/admin/images"
                    :show-file-list="false"
                    :on-success="handleMapAvatarSuccess"
                    name="image[file]"
                    :before-upload="beforeAvatarUpload">
            <i class="el-icon-plus avatar-uploader-icon" />
          </el-upload>
        </el-form-item>
        <!-- 地图图片end-->
        <el-form-item label="封面跳转地址" prop="cover_link">
          <el-input v-model="form.cover_link" placeholder="请输入封面跳转地址"  />
        </el-form-item>
        <el-form-item label="均价">
          <el-input v-model="form.price_per_sqm" placeholder="无定价" style="margin-right:10px" />
        </el-form-item>
        <el-form-item label="关键词">
          <el-tag
            v-for="(tag, index) in form.tags"
            :key="tag + index"
            style="margin-right:10px"
            closable
            :disable-transitions="false"
            @close="handleClose(tag)">{{tag}}</el-tag>
          <el-input
            class="input-new-tag"
            v-if="inputVisible"
            v-model="inputValue"
            ref="saveTagInput"
            size="small"
            @keyup.enter.native="handleInputConfirm"
            @blur="handleInputConfirm"
          />
          <el-button v-else class="button-new-tag" size="small" @click="showInput">+ 添加</el-button>
        </el-form-item>
        <el-form-item label="楼盘地址" prop="address" required>
          <el-input v-model="form.address" placeholder="请输入楼盘地址"  />
        </el-form-item>
        <el-form-item label="咨询电话" prop="phone">
          <el-input v-model="form.phone" placeholder="请输入咨询电话"/>
        </el-form-item>
        <el-form-item prop="base_count" label="关注基数">
          <el-input v-model="form.base_count" placeholder="请输入关注基数" />
        </el-form-item>
        <el-form-item label="免责条款">
          <el-input v-model="form.disclaimer" placeholder="请输入免责条款"  />
        </el-form-item>
        <h1 style="font-size: 20px">楼盘详情</h1>
        <el-form-item label="开发商">
          <el-input v-model="form.developer" placeholder="请输入开发商"  />
        </el-form-item>
        <el-form-item label="物业公司">
          <el-input v-model="form.property_company" placeholder="请输入物业公司"  />
        </el-form-item>
        <el-form-item label="开盘时间">
          <!-- <el-date-picker v-model="form.opening_date" type="date" placeholder="请输入开盘时间" /> -->
          <el-input v-model="form.opening_date" placeholder="请输入开盘时间"  />
        </el-form-item>
        <el-form-item label="交楼时间">
          <!-- <el-date-picker v-model="form.delivery_date" type="date" placeholder="请输入交楼时间" /> -->
          <el-input v-model="form.delivery_date" placeholder="请输入交楼时间"  />
        </el-form-item>
        <el-form-item label="物业类型">
          <el-input v-model="form.property_type" placeholder="请输入物业类型"  />
        </el-form-item>
        <el-form-item label="产权年限">
          <el-input v-model="form.property_year" placeholder="请输入产权年限"  />
        </el-form-item>
        <el-form-item label="车位数量">
          <el-input v-model="form.car_position_count" placeholder="请输入车位数量"  />
        </el-form-item>
        <el-form-item label="装修标准">
          <el-input v-model="form.decoration_standard" placeholder="请输入装修标准"  />
        </el-form-item>
        <el-form-item label="绿化率">
          <el-input v-model="form.green_rate" placeholder="请输入绿化率"  />
        </el-form-item>
        <el-form-item label="容积率">
          <el-input v-model="form.plot_ratio" placeholder="请输入容积率"  />
        </el-form-item>
        <el-form-item label="物业费">
          <el-input v-model="form.property_fee_desc" placeholder="请输入物业费"  />
        </el-form-item>
        <div
          v-for="(display, displayIndex) in form.building_displays"
          :key="display.id">
          <h1 style="font-size: 20px">{{ display.name }}</h1>
          <template v-for="(displayPictures, displayPicturesIndex) in display.building_display_pictures">
            <div v-if="!displayPictures._destroy" :key="displayPictures.id" style="margin-top:20px">
              <el-form-item label="类目">
                <el-input v-model="displayPictures.name" placeholder="请输入类目" style="margin-right:10px"/>
                <el-button type="danger" @click.prevent="removeDisplayPictures(displayIndex, displayPicturesIndex)">删除</el-button>
              </el-form-item>
              <div style="margin-top:10px">
                <div
                  v-for="(picture, pictureIndex) in displayPictures.pictures"
                  v-if="!picture._destroy"
                  :key="picture.id"
                  class="avatar">
                  <img :src="'https://szebra.cn' + picture.url" />
                  <i class="el-icon-circle-close" @click="removeBuildingPictures(displayIndex, displayPicturesIndex, pictureIndex)"/>
                </div>
                <el-upload class="avatar-uploader"
                        action="https://szebra.cn/api/admin/images"
                        :show-file-list="false"
                        :on-success="handleBuildingPictureSuccess(displayIndex, displayPicturesIndex)"
                        name="image[file]"
                        :before-upload="beforeAvatarUpload">
                  <i class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </div>
            </div>
          </template>
          <el-button plain @click.prevent="addBuildingDisplayPictures(displayIndex)" style="margin-top:10px; margin-bottom:20px">+ 添加图片类目</el-button>
        </div>

        <h1 style="font-size: 20px">楼盘亮点</h1>
        <el-form-item label="亮点概述">
          <el-input v-model="form.building_description.sub_desc" placeholder="请输入亮点概述"  />
        </el-form-item>
        <el-table
          :data="form.building_description.building_description_details.filter(detail => !detail._destroy)"
          style="width: 100%">
          <el-table-column type="index" width="100" />
          <el-table-column property="title" label="亮点标题" >
            <template slot-scope="scope">
              <el-input v-model="scope.row.title" type="text" size="small"/>
            </template>
          </el-table-column>
          <el-table-column property="desc" label="亮点内容" >
            <template slot-scope="scope">
              <el-input v-model="scope.row.desc" type="text" size="small"/>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button @click.native.prevent="deleteRow(scope.$index)" type="text" size="small">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div style="margin-top: 10px">
          <el-button @click="addRow">添加亮点</el-button>
        </div>
        <el-button type="primary" @click="onSubmit" style="margin-top: 20px">{{submitBtnTitle}}</el-button>
      </el-form>
    </el-row>
  </div>
</template>
<script>

export default {
  data () {
      return {
        inputVisible: false,
        inputValue: '',
        pageTitle: '', // 页面标题
        submitBtnTitle: '', // 页面标题
        form: {
          name: '',
          cover: '',
          cover_link: '',
          poster: '',
          map: '',
          price_per_sqm: '',
          tags: [],
          address: '',
          phone: '',
          property_company: '',
          developer: '',
          opening_date: '',
          delivery_date: '',
          property_type: '',
          property_year: '',
          car_position_count: '',
          decoration_standard: '',
          green_rate: '',
          plot_ratio: '',
          disclaimer: '',
          property_fee_desc: '',
          building_displays: [
            {
              name: '楼盘近景',
              building_display_pictures: [
                {
                  name: '',
                  pictures: []
                }
              ]
            }
          ],
          building_description: {
            sub_desc: '',
            building_description_details: []
          }
        }
      }
  },
  methods: {
    formatDate (datetime) {
      if (!datetime) return ''
      var date = new Date(datetime)
      date.setHours(date.getHours() + 8)
      return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`
    },
    formatSubmitValue() {
      if (!this.form.cover || !this.form.cover.id) {
        this.$message.error('封面不可为空')
        return
      }
      if (!this.form.address) {
        this.$message.error('地址不可为空')
        return
      }

      const ret = { id: this.form.id, building: Object.assign({}, this.form) }
      console.log('ret', ret)
      ret.building.opening_date = this.formatDate(ret.building.opening_date)
      ret.building.delivery_date = this.formatDate(ret.building.delivery_date)

      // 设置封面图
      if (this.form.cover && this.form.cover.id) {
        ret.building.cover_attributes = {
          id: this.form.cover.id
        }
      }
      // 设置海报图
      if (this.form.poster && this.form.poster.id) {
        ret.building.poster_attributes = {
            id: this.form.poster.id
          }
      }

      // 设置地图图片
      if (this.form.map && this.form.map.id) {
        ret.building.map_attributes = {
            id: this.form.map.id
          }
      }
  
      // format building_displays
      ret.building.building_displays = undefined
      ret.building.building_displays_attributes = this.form.building_displays.map(display => {
        const ret = Object.assign({}, display)
        ret.building_display_pictures_attributes = ret.building_display_pictures
        ret.building_display_pictures = undefined
        ret.building_display_pictures_attributes = ret.building_display_pictures_attributes.map(displayPictures => {
          const ret = Object.assign({}, displayPictures)
          ret.pictures_attributes = ret.pictures
          ret.pictures = undefined
          return ret
        })
        return ret
      })
      // format building_description
      ret.building.building_description = undefined
      ret.building.building_description_attributes = Object.assign({}, this.form.building_description)
      ret.building.building_description_attributes.building_description_details = undefined
      ret.building.building_description_attributes.building_description_details_attributes = this.form.building_description.building_description_details
      console.log(ret)
      return ret

    },
    handleAvatarSuccess(data) {
      this.form.cover = data.result_rows
    },
    handlePosterAvatarSuccess(data){
      this.form.poster = data.result_rows
    },
    handleMapAvatarSuccess(data){
      this.form.map = data.result_rows
    },
    beforeAvatarUpload(file) {
      const isImg = file.type.startsWith('image/');
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isImg) {
        this.$message.error('只允许上传图片');
      }
      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过 2MB!');
      }
      return isImg && isLt2M;
    },
    handleChange(){},
    removeConverImageUrl () {
      this.form.cover = ''
    },
    removePosterImageUrl () {
      this.form.poster = ''
    },
    removeMapImageUrl () {
      this.form.map = ''
    },
    handleClose(tag) {
      this.form.tags.splice(this.form.tags.indexOf(tag), 1)
    },
    showInput() {
      this.inputVisible = true
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    handleInputConfirm() {
      let inputValue = this.inputValue
      if (inputValue) {
        this.form.tags.push(inputValue)
      }
      this.inputVisible = false
      this.inputValue = ''
    },
    deleteRow(index) {
      const details = this.form.building_description.building_description_details
      details[index]._destroy = true
      details.splice(index, details[index].id ? 0 : 1)
    },
    addRow () {
      this.form.building_description.building_description_details.push({})
    },
    removeDisplayPictures(displayIndex, displayPicturesIndex) {
      const displayPictures = this.form.building_displays[displayIndex].building_display_pictures
      displayPictures[displayPicturesIndex]._destroy = true
      displayPictures.splice(displayPicturesIndex, displayPictures[displayPicturesIndex].id ? 0 : 1)
    },
    addBuildingDisplayPictures (displayIndex) {
      this.form.building_displays[displayIndex].building_display_pictures.push({
        name: '',
        pictures: []
      })
    },
    removeBuildingPictures (displayIndex, displayPicturesIndex, pictureIndex) {
      const pictures = this.form.building_displays[displayIndex].building_display_pictures[displayPicturesIndex].pictures
      pictures[pictureIndex]._destroy = true
      pictures.splice(pictureIndex, pictures[pictureIndex].id ? 0 : 1)
    },
    handleBuildingPictureSuccess (displayIndex, displayPicturesIndex) {
      const displayPictures = this.form.building_displays[displayIndex].building_display_pictures
      return data => displayPictures[displayPicturesIndex].pictures.push(data.result_rows)
    },
    // 添加图片组
    addPictures () {
      this.form.building_displays[0]['building_display_pictures'].push({})
    },
    onSubmit () {
      const data = this.formatSubmitValue()
      if (!data) return
      this.$http[this.submitFuncName](data).then(res => {
        if (res.data.retcode == 0) {
          this.$message.success('保存成功')
          Object.assign(this.form, res.data.result_rows)
          this.$router.push({ path: '/property/list'})
        } else {
          this.$message.error('保存失败');
        }
      })
    }
  },
  created () {
    switch (this.$router.currentRoute.name) {
        case 'property_create':
          this.pageTitle = '新增楼盘'
          this.submitBtnTitle = '提交'
          this.submitFuncName = 'buildingCreate'
          break
        default:
          this.pageTitle = '编辑楼盘'
          this.submitBtnTitle = '保存所有编辑结果'
          this.submitFuncName = 'buildingUpdate'
          this.$http.buildingDetail(this.$router.currentRoute.params)
            .then(res => {
              if (!res) return
              Object.assign(this.form, res.data.result_rows)
              if (!this.form.building_description) {
                this.form.building_description = {
                  sub_desc: '',
                  building_description_details: []
                }
              }
            })
      }
  }
}
</script>

<style>
  .el-input {
    width: 270px;
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    display: inline-block;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    position: relative;
    float: left;
    margin-right: 5px;
  }
  .avatar img {
    margin-top: 6px;
    margin-right: 6px;
    width: 178px;
    height: 178px;
  }
  .avatar i {
    position: absolute;
    top: 0;
    right: 0;
    opacity: 0.6;
  }
  .avatar i:hover {
    opacity: 1;
    cursor: pointer;
  }
</style>
